.loginbox {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    z-index: 2; 
    position: relative;
}



.overflowhidden {
    overflow-y: hidden;
}

.globe {
    width: 100px;
    height: 100px;
    background: radial-gradient(circle at 100px 100px, #3f7dc0, #b84545);
    border-radius: 50%;
    position: absolute;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2), inset 0 -5px 10px rgba(0, 0, 0, 0.2);
   
    z-index: 0;
}

.globe1 {
    width: 150px;
    height: 150px;
    transform: rotate(180deg);
    top: 5%;
    left: 30%;
   
}

.globe2 {
    height: 200px;
    width: 200px;
    top: 70%;
    left: 80%;
    
}

.globe3 {
    
    top: 50%;
    left: 10%;
   
}

.title{
    margin: 0;
    padding: 0 0 0.5rem;
    font-size: 3rem;
    font-weight: bold;
    color: rgba(255, 255, 255, 1.1);
    font-family: monospace;
    text-align: center;
}

.subtitle{
    margin: 0;
    padding: 0 0 20px;
    font-size: 1.5rem;
    color: rgba(255, 255, 255, 0.7);

    font-family: monospace;
    text-align: center;
}


.oauth{
    text-align: center;
    
    border-radius: 15px;
    border: 2px solid #3f7dc0;
    padding: 0.5rem;
    font-family: "inter",sans-serif;
    color: antiquewhite;
    margin-top: 20px;
    width: 35%;
    font-size: 1.2rem;
    transition: transform 0.2s;
}

.oauth:hover{
    transform: scale(1.05);
    cursor: pointer;
}

.divider{
    margin-top: 0.5rem;
    text-align: center;
    font-family: monospace;
    font-size: 1.4rem;
    color: rgba(255, 255, 255, 0.5);
}

.form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    width: 30%;
}

.input{
    width: 100%;
    padding: 0.5rem;
    margin: 0.5rem;
    border-radius: 15px;
    text-align: center;
    background-color:  rgba(0, 0, 0,0.1);
    border: 5px solid #2e2d2d60;
    font-family: "inter",sans-serif;
    color: antiquewhite;
    font-size:1rem;
    transition: transform 0.2s;
}

.input:hover{
    transform: scale(1.05);
    cursor: pointer;
}

.button {
    width:50%;
    background-color:  rgba(0, 0, 0,0.1);;
    
    color: white;
    border: 2px solid #3f7dc0;
    padding: 0.5rem;
    margin: 8px 0;
   
    border-radius: 10px;
    cursor: pointer;
    font-size: 1.1rem;

    transition:  0.3s ease, transform 0.2s ease;
}

button:hover {
    transform: scale(1.05);
}

button:active {

    transform: scale(1);
}
.error {
    color: red;
    margin-bottom: 15px;
    font-size: 0.9rem;
}