.main{
    display: flex;
    width: 100vw;
    height: 100vh;
}

.sidebar{
    
    width: 25%;
    border-right: 3px solid #2e2d2d60;
    height: 100vh;
    display: flex;
    flex-direction: column;
    
}

.title{
    padding-top: 0.8rem;
    padding-bottom: 0.4rem;
    padding-left: 0.3rem;
    padding-right: 1rem;
    border-bottom: 3px solid #2e2d2d60;;
    font-family: "inter",sans-serif;
    font-size: 1.5rem;
    color: antiquewhite;
    margin-bottom: 0.3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 10%;
}

.chatWindow{
    width: 75%;
    display: flex;
    flex-direction: column;
}

.WindowTitle{
    height: 10%;
    padding-top: 1.2rem;
    padding-bottom: 0.4rem;
    padding-left: 0.6rem;
    padding-right: 1rem;
    border-bottom: 3px solid #2e2d2d60;;
    font-family: "inter",sans-serif;
    font-size: 0.7rem;
    color: antiquewhite;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.chatWindowContent{
    height: 80%;
    overflow-y: auto;
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.chatWindowInput{
    height: 10%;
    padding: 0.5rem;
    display: flex;
   
    align-items: center;
    gap: 0.5rem;
}

.input{
    width: 80%;
    height: 100%;
    padding: 0.5rem;
    border-radius: 8px;
    border: 2px solid #2e2d2d60;
    background-color: black;
    color: antiquewhite;
    font-family: "inter",sans-serif;
    font-size: 1rem;
}

.input:focus{
    outline: solid 2px #2e2d2d60;
}

.button{
    width: 15%;
    height: 100%;
    padding: 0.5rem;
    border-radius: 5px;
    border: 2px solid #2e2d2d60;
    font-family: "inter",sans-serif;
    font-size: 1rem;
    background-color: #2e2d2d60;
    color: antiquewhite;
    transition: transform 0.2s;
}

.channel{
    padding-left: 0.8rem;
    padding: 0.4rem;
    border-bottom: 2px solid #2e2d2d60;
    font-family: "inter",sans-serif;
    font-size: 1rem;
    font-weight: bold;
    color: antiquewhite;
    cursor: pointer;
   
    transition: background-color 0.2s;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.channelname{
    text-wrap: wrap;

}
.addChannel:hover{
    cursor: pointer;
    color:#3f7dc0
    
}

.channel:hover{
    background-color: #2e2d2d60;
    color:#3f7dc0
}

.enterchannel{
    font-size: 1.2rem;
}


.messageContainer{
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 0.2rem;
    align-items: flex-start;
}

.author{
    padding-left: 0.3rem;
    font-size: 0.9rem;
    color: #3f7dc0;
    font-family: "inter",sans-serif;
}
.message{
    display: inline-flex;
    min-width: 15%;
    max-width: 90%;
    text-wrap: wrap;
    word-wrap: break-word;
    padding: 0.5rem;
    border-radius: 8px;
    background-color: #2e2d2d60;
    color: antiquewhite;
    font-family: "inter",sans-serif;
    padding: 0.5rem;
    padding-left: 0.8rem;
    font-size: 1rem;
    margin-bottom: 0.5rem;
}


.toggleContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1rem 0;
}

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
    border-radius: 34px;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
    border-radius: 50%;
}

input:checked + .slider {
    background-color: #2196F3;
}

input:checked + .slider:before {
    transform: translateX(26px);
}

.ailabel{
    font-family: "inter",sans-serif;
    font-size: 1.2rem;
    color: antiquewhite;
    margin-right: 0.5rem;
}

.loadingSpinner {
    border: 4px solid #030303; /* Light grey */
    border-top: 4px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 24px;
    height: 24px;
    animation: spin 2s linear infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}


.menu {
    cursor: pointer;
    display: inline-block;
    margin-right: 0.6rem;
  
}

.menu p {
    margin: 0;
    line-height: 0.5rem;
}
.settings {
    font-size: 1.5rem;
    position: relative;
    display: inline-block;
    margin-right:0.4rem ;
}

.settings .fa-cog {
    cursor: pointer;
}

.dropdownMenu {
    display: none;
    position: absolute;
    background-color: #2e2d2d;
    color: wheat;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}

.settings:hover .dropdownMenu {
    display: block;
}

.altchatWindow{
    width: 75%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: antiquewhite;
    font-family: "inter",sans-serif;
}

.infoTitle{
    font-size: 4rem;
    margin-bottom: 1rem;
}

.infoSubtitle{
    font-size: 1.5rem;
    margin-bottom: 4rem;
    color:#646262d8
}

.inforow{
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 1rem;
    margin-bottom: 1rem;
    justify-content: space-evenly;
}

.infoBox{
    display: flex;
    flex-direction: column;
    width: 30%;
    text-align: center;
    gap: 1rem;
    margin-bottom: 1rem;
}

.infoBox h1{
    font-size: 1.6rem;

}